/* eslint-disable no-alert */
/* eslint-disable react/prop-types */
/* eslint-disable react/no-multi-comp */
/* eslint-disable jsx-a11y/href-no-hash */
/* eslint-disable jsx-a11y/label-has-for */
import React, { Component } from 'react';
import { dripForm } from '../../../../src/';
import { Layout, Button, Code } from '../../components/';
import { Input } from '../../fields/';


// Simulate API
const requestLogin = (email, password) => new Promise((resolve, reject) => {
  setTimeout(() => {
    if (email === 'example@mail.com' && password === 'passwd') {
      resolve();
    } else {
      reject();
    }
  }, 1000);
});


const SubmitValidationForm = dripForm()(({
  handlers,
  meta: { invalid, pristine },
  submitting,
}) => (
  <form onSubmit={handlers.onSubmit}>
    <div>
      <label htmlFor="email">Email-Address</label>
      <Input
        type="email"
        id="email"
        name="email"
        label="Email-Address"
        placeholder="enter-your-email@example.com"
        validations={{
          required: true,
          email: true,
        }}
      />
    </div>

    <div>
      <label htmlFor="email">Password</label>
      <Input
        type="password"
        id="password"
        name="password"
        label="Password"
        validations={{
          required: true,
        }}
      />
    </div>

    <Button
      onClick={handlers.onSubmit}
      disabled={invalid || pristine || submitting}
    >
      {submitting ? 'Logging in...' : 'Login'}
    </Button>
  </form>
));


class MyComponent extends Component {
  state = {
    submitting: false,
  };

  handleInitialize = (form) => {
    this.form = form;
  };

  handleSubmit = ({ email, password }) => {
    this.setState({ submitting: true });

    requestLogin(email, password)
      .then(() => {
        this.setState({ submitting: false });
        this.form.clear();
        alert('Login succeeded!');
      })
      .catch(() => {
        this.form.setErrors({ email: 'Email-Address or password is incorrect.' });
        this.setState({ submitting: false });
      });
  };

  render() {
    return (
      <SubmitValidationForm
        {...this.props}
        submitting={this.state.submitting}
        onInitialize={this.handleInitialize}
        onValidSubmit={this.handleSubmit}
      />
    );
  }
}


export default class BasicFormExample extends Component {
  state = {
    values: {},
  };

  render() {
    const { location } = this.props;
    const { values } = this.state;

    return (
      <Layout
        title="Submit Validation"
        location={location}
      >
        <style jsx>{`
          :global(form > div) {
            margin-bottom: 1em;
          }

          :global(form > div > label) {
            display: block;
            margin: 0 0 0.2em;
            font-weight: bold;
          }
        `}</style>

        <p>
          todo...
        </p>

        <hr />

        <h3>Example:</h3>
        <MyComponent
          onChange={v => this.setState({ values: v })}
        />
        <hr />

        <h3>Values:</h3>
        <Code language="javascript">{JSON.stringify(values, null, 2)}</Code>
        <hr />

        <h3>Sample Code:</h3>
        <Code language="javascript">{`import React, { Component } from 'react';
import { dripForm } from 'react-drip-form';
`}</Code>
      </Layout>
    );
  }
}



